<template>
    <div class="w-full flex flex-row justify-around justify-items-center ">
        <div class="h-[80vh] bg-gray-400 rounded-md w-3/12 flex flex-col justify-around justify-items-center  items-center" >
            <p>Rot: {{rot}}deg</p>
            <input type="range" min="-180" max="180" step="5" v-model="rot"/>

            <div class="flex flex-col gap-5 justify-center justify-items-center h-auto">
                <input type="color" v-model="colorPic"/>
                <!-- <input type="range" min="0" max="100" v-model="porcent" /> -->
                <input type="range" min="0" max="1" step="0.01" v-model="trans" />
                <button class="bg-blue-400 rounded-md p-3" @click="addColor">+</button>
            </div>

            <div class="bg-gray-200 overflow-y-scroll w-full h-20" >
                <div v-for="{id,color} in colors" :key="id">
                    <p>{{color}}<button class="bg-red-400 rounded-md px-3" @click="deleteColor(id)">-</button></p>
                </div>
            </div>
            

            <CSS :text="styles"/>

        </div>
        
        <CanvasDiv :styles="styles" :img="img">
            
        </CanvasDiv> 
    
    </div>

    
</template>

<script>

import CanvasDiv from '@/components/CanvasDiv.vue'
import CSS from '@/components/CSSgen.vue'
export default {
  components: {CanvasDiv,CSS },
    data() {

        return {
            idColor:0,
            trans:1,
            porcent:100,
            rot:0,
            colors:[],
            colorPic:'#ffffff',
            
            img:`background-image: url(${this.url})`,
            styles:'',
            
        }
    },

    methods: {
        css(){

            if(this.colors.length >= 2){
                var colores = ``;
                for (let i = 0; i < this.colors.length; i++) {
                    colores += `,${this.colors[i].color}`;
                    
                }
                this.styles=`
                background: linear-gradient(${this.rot}deg ${colores});
                `

            }else{
                this.styles=`
                background: linear-gradient(${this.rot}deg,#000000,#ffffff);
                `;
            }

        },
        resetValue(){
            this.porcent=100;
            this.trans=1;
            this.colorPic='#ffffff';
        },
        addColor(){
            var newColor=this.colorPic.split('#')[1];
            this.colors.push({id:this.idColor,color:`rgba(${this.hexToRgb(newColor)},${this.trans}) `});
            //${this.porcent}%
            this.idColor++;
            this.resetValue();
            this.css();
            
        },
        deleteColor(id){
         
            var lista = [];

            for (let i = 0; i < this.colors.length; i++) {
                if(this.colors[i].id != id){
                    lista.push(this.colors[i])
                };  
            }
            this.colors=lista;
            this.css();
        },
        hexToRgb(hex) {
            var bigint = parseInt(hex, 16);
            var r = (bigint >> 16) & 255;
            var g = (bigint >> 8) & 255;
            var b = bigint & 255;

            return r + "," + g + "," + b;
        }

    },
    mounted() {
        
        this.css();
    },
    watch:{
        rot(){
            this.css();
        }
        
    }

}
</script>

