<template>
  <div class="shadow-md rounded-md w-4/6 bg-yellow-300 flex justify-center justify-items-center items-center background gap-5" >

    <div class="canvas flex justify-center justify-items-center" :style="colorInicio">
      
    </div>
    <div class="canvas flex justify-center justify-items-center" :style="colorP2">
      
    </div>
    <div class="canvas flex justify-center justify-items-center" :style="colorP3">
      
    </div>
    <div class="canvas flex justify-center justify-items-center" :style="colorP4">
      
    </div>

  </div>
</template>

<script>
import * as tf from '@tensorflow/tfjs';

import json from '../assets/model.json'
export default {
  props:{
    color: String
  },
  data() {
    return {
      colorIni:this.color,
      color1:'#ffffff',
      color2:'#ffffff',
      color3:'#ffffff',

      colorInicio:`background-color: #ffffff`,
      colorP2:`background-color: #ffffff`,
      colorP3:`background-color: #ffffff`,
      colorP4:`background-color: #ffffff`,
    }
  },
  methods: {
    async ia(){
      this.colorIni=this.color
      var color = this.hexToRgb(this.hexToRgb(this.colorIni.split('#')[1]))
      const model = await tf.loadLayersModel('./model.json');
      var tensor = tf.tensor([[color[0]/255, color[1]/255, color[2]/255,]])
      var tensor2 = model.predict(tensor);
      var values = tensor2.dataSync();
      var arr = Array.from(values);

      this.colorP2=`background-color: rgb(${Math.round(arr[0]*255)},${Math.round(arr[1]*255)},${Math.round(arr[2]*255)})`;
      this.colorP3=`background-color: rgb(${Math.round(arr[3]*255)},${Math.round(arr[4]*255)},${Math.round(arr[5]*255)})`;
      this.colorP4=`background-color: rgb(${Math.round(arr[6]*255)},${Math.round(arr[7]*255)},${Math.round(arr[8]*255)})`;
      this.colorInicio=`background-color: ${this.colorIni}`;
      
    },
    hexToRgb(hex) {
      var bigint = parseInt(hex, 16);
      var r = (bigint >> 16) & 255;
      var g = (bigint >> 8) & 255;
      var b = bigint & 255;

      return [r, g, b];
    }
  },
  mounted(){
    this.ia()
  },
  watch:{
    color(){
      this.ia()
    }
  }

}
</script>

<style lang="css" scoped>
  .canvas{
    
    border-radius: 15px;
    width: 100px;
    height: 250px;
  }
  .background{
    /* background-image: url('https://www.xtrafondos.com/descargar.php?id=6190&resolucion=1920x1080'); */
    background-image: url('https://blog.stockvault.net/wp-content/uploads/2017/10/grunge.jpg');
    background-repeat: no-repeat;
    background-size: cover;
  }
</style>