
const sidebar =(perfil=true,ColorhoverBtn='#ff0000',background='rgba(255,255,255,0.18)',numSec=2,blur=6)=>{

    let nav =`
    <div class="flex_nav">
    <nav class="sidebar">
        <div class="column">
            
                <a href="#" class="brand">
                    <!--Icono-->
                    <span class="meterial-icons-outlined">
                        <i class="fa-solid fa-layer-group"></i>
                    </span>
                    <!--Nombre principal-->
                    <span>SideBar</span> 
                </a>
            <div class="over">
            `
            for (let i = 0; i < numSec; i++) {
                nav += `
                <small class="menu-heading">
                <span>Sección ${i}</span>
            </small>
            <ul class="tools">
                <li>
                    <a href="#">
                        <span class="meterial-icons-outlined">
                            <i class="fa-solid fa-newspaper"></i>
                        </span>
                        <span>Link${i}</span>
                    </a>
                </li>

            </ul>
            <hr/>`
                
            }
            nav+=`
            </div>`
            
            if(perfil){
                nav+=`
                <!--Seccion Perfil-->
                <div class="profile">
                    <a href="#">
                        <img src="https://source.unsplash.com/d2MSDujJl2g/50x50"/>
                    </a>
                    <div>
                        <h4>Nombre</h4>
                        <small>Rol</small>
                    </div>
                </div>`
            }

            
    nav+=`
        </div>
    </nav>

    <div class="resto">

    </div>
</div>




<style>
    .flex_nav{
        display: flex;
        flex-direction: row;
        height:100%
    }
    .resto{
        width: 100%;
        
        height: 100%;
        background-color: #2a2f44;
    }


    .column{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .over{
        min-height: 80%;
        overflow-y: scroll;
    }
    .sidebar{
        background: ${background}; /*Color sidebar*/
        color: #fff;
        box-shadow: 0 8px 32px 0
        rgba(0,0,0,0.37);
        backdrop-filter: blur(${blur}px);
        border: 1px solid rgba(0,0,0,0.3);
        /*position: fixed;
        top: 0;
        left: 0;*/
        width: 200px;
        height: 100%;
        overflow-y: hidden;
    }
    .sidebar ::-webkit-scrollbar{
        width: 5px;
    }
    .sidebar ::-webkit-scrollbar-track{
        background: transparent;
    }
    .sidebar ::-webkit-scrollbar-thumb{
        background:  #2a2f44;
        border-radius: 10px;
    }
    .sidebar > div{
        height: 100%;
        overflow-y: scroll;
        padding: 1.7em 1em 1em 1em;
    }
    .sidebar a{
        text-decoration: none;
    }
    .sidebar ul{
        margin: 0;
        padding: 0;
        list-style-type: none;
    }
    .sidebar small{
        color: #ccc
    }
    .brand{
        height: 10%;
        display: block;
        font-size: 1.5em;
        color: #fff;
        font-weight: 600;
    }
    .menu-heading{
        padding: 1em;
        margin: 2em 0 0.5em 0;
        display: block;
    }
    .sidebar ul{
        padding-bottom: 1.5em;
    }
    .sidebar li a {
        color:#ccc;
        display: flex;
        flex-direction: row;
        justify-content:space-between;
        justify-items: center;
        align-items: center;
        padding: 1em;
        border-radius:  0.8em;
        transition: 0.3s;
    }
    .tools li a:hover{
        background: ${ColorhoverBtn};
        color: #fff;
    }
    .sidebar ul .meterial-icons-outlined{
        font-size: 1.6em;
        margin-left: 0.5em;
    }
    .sidebar hr{
        border: 1px #2a2f44 solid;
        width: 90%;
        margin: 0 auto;
    }

    .profile{
        height: 10%;
        bottom: 0;
       /* padding:  1.7em 1em 0em 1em;*/
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .profile img{
       width: 2.5em;
       height: 2.5em;
       border-radius: 50%;
       display: inline-block;
       text-align: center; 
    }
    .profile h4{
        margin:  0 0 0.5em 0;
    }

    @media screen and (max-width: 800px) {
        .resto{
            width: 90%;
        }
        .sidebar{
           width: 5em;
            font-size: 14px;
            text-align: center;
        }
        .sidebar > div{
            padding: 1.7em 0.2em 1.7em 0.3em;
        }
        .sidebar ul .meterial-icons-outlined{
            font-size: 1.6em;
            margin-left: 0px;
        }
        .sidebar span:not(.meterial-icons-outlined),
        .profile div, .profile span {
            display: none;
        }
        .menu-heading, .profile {
            margin-top: 0;
        }
        .profile{
            justify-content: center;
        }

    }
</style>
    
    `;


    return nav;
}


const dropdown=(colorHoverDrop,colorTextHover,colorDrop,colorText,colorBorder)=>{
    let drop=`

    <div class="dropdown">
        <button class="dropdown_hover" >Dropdown</button>
        <div class="dropdown_menu">
            <a href="#">D1</a>
            <a href="#">D2</a>
            <a href="#">D3</a>
        </div>
    </div>
    
    <style>
    .dropdown{
        position: relative;
        z-index: 10;
        display: inline-block;
      }
        .dropdown_hover{
          display: inline-block;
          padding: 10px;
          background-color:#323b40;
          color: #ffffff;
          border:0 solid #323b40;
          cursor: pointer;
        }
        .dropdown_hover:hover{background-color: #222222;}
        .dropdown_menu{
          position: absolute;
          display: none;
          top: 100%;
          left: 0;
          min-width: 200px;
          background-color: #ffffff;
          color: #323b40;
          box-shadow: 0 3px 2px rgba(0,0,0,0.3);
        }
        .dropdown:hover .dropdown_menu{display: block;}
          .dropdown_menu a{
            display: block;
            padding: 10px;
            text-decoration: none;
            border-width: 0;
            background-color: ${colorDrop};
            color: ${colorText};
            border-top: 1px solid ${colorBorder};
          }
          .dropdown_menu a:hover{
            background-color:${colorHoverDrop};
            color: ${colorTextHover};
          }
    </style>
    `;

    return drop;
}

const navbar=(color,blur,text,clase)=>{
    let nav =`
    <nav class="${clase}_navbar">
    <input id="${clase}_nav-toggle" type="checkbox">
    <div class="${clase}_logo" >
        WEB
    </div>
    <ul class="${clase}_links">
        <li><a href="#">Link1</a></li>
        <li><a href="#">Link2</a></li>
        <li><a href="#">Link3</a></li>
        <li><a href="#">Link4</a></li>
    </ul>
    <label for="${clase}_nav-toggle" class="${clase}_icon-burger">
        <div class="${clase}_line"></div>
        <div class="${clase}_line"></div>
        <div class="${clase}_line"></div>
    </label>
</nav>

<div class="${clase}_container">

</div>

<style>

    .${clase}_container{
        position: relative;
        margin-top: 100px;
        width: 100%;

    }
    .${clase}_navbar {
        position: relative;
        z-index: 15;
        left: 0;
        right: 0;
        top: 0;
        font-family: 'Montserrat', sans-serif;
        padding: 0 5%;
        height: 100px;
        background-color: ${color};
        backdrop-filter: blur(${blur}px);
    }
    .${clase}_navbar .${clase}_logo {
        float: left;
        width: 40%;
        height: 100%;
        display: flex;
        align-items: center;
        font-size: 24px;
        color: ${text};
    }
    .${clase}_navbar .${clase}_links {
        float: right;
        position: relative;
        padding: 0;
        margin: 0;
        width: 60%;
        height: 100%;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    .${clase}_navbar .${clase}_links li {
        list-style: none;
    }
    .${clase}_navbar .${clase}_links a {
        display: block;
        padding: 1em;
        font-size: 16px;
        font-weight: bold;
        color: ${text};
        text-decoration: none;
    }
    #${clase}_nav-toggle {
        position: absolute;
        top: -100px;
    }
    .${clase}_navbar .${clase}_icon-burger {
        display: none;
        position: absolute;
        right: 5%;
        top: 50%;
        transform: translateY(-50%);
    }
    .${clase}_navbar .${clase}_icon-burger .${clase}_line {
        width: 30px;
        height: 5px;
        background-color: ${text};
        margin: 5px;
        border-radius: 3px;
        transition: all .3s ease-in-out;
    }
    @media screen and (max-width: 768px) {
        .${clase}_navbar .${clase}_logo {
            float: none;
            width: auto;
            justify-content: center;
        }
        .${clase}_navbar .${clase}_links {
            float: none;
            position: fixed;
            z-index: 9;
            left: 0;
            right: 0;
            top: 100px;
            bottom: 100%;
            width: auto;
            height: auto;
            flex-direction: column;
            justify-content: space-evenly;
            background-color: rgba(0,0,0,.8);
            overflow: hidden;
            box-sizing: border-box;
            transition: all .5s ease-in-out;
        }
        .${clase}_navbar .${clase}_links a {
            font-size: 20px;
        }
        .${clase}_navbar :checked ~ .${clase}_links {
            bottom: 0;
        }
        .${clase}_navbar .${clase}_icon-burger {
            display: block;
        }
        .${clase}_navbar :checked ~ .${clase}_icon-burger .${clase}_line:nth-child(1) {
            transform: translateY(10px) rotate(225deg);
        }
        .${clase}_navbar :checked ~ .${clase}_icon-burger .${clase}_line:nth-child(3) {
            transform: translateY(-10px) rotate(-225deg);
        }
        .${clase}_navbar :checked ~ .${clase}_icon-burger .${clase}_line:nth-child(2) {
            opacity: 0;
        }
        
    }
</style>
    `;

    return nav;
}

const login1=(colorBack,colorText,colorLogin,colorLoginText,colorLoginHover,colorLoginTextHover)=>{

        let log=`
        <div class="login-box">
        <img src="https://images.unsplash.com/photo-1635492491273-455af7728453?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=660&q=80" class="avatar" alt="Avatar Image">
        <h1>Login</h1>
        <form class="form">
          
          <label for="username">Username</label>
          <input type="text" placeholder="Enter Username">
          
          <label for="password">Password</label>
          <input type="password" placeholder="Enter Password">
          <input type="submit" value="Log In">
          <a href="#">Link1</a><br>
          <a href="#">Link2</a>
        </form>
    </div>

    <style>

        .form{
            text-align: left;
        }
          
          .login-box {
            width: 320px;
            height: 420px;
            background: ${colorBack};
            color: ${colorText};
            top: 50%;
            left: 50%;
            position: absolute;
            transform: translate(-50%, -50%);
            box-sizing: border-box;
            padding: 70px 30px;
          }
          
          .login-box .avatar {
            width: 100px;
            height: 100px;
            border-radius: 50%;
            position: absolute;
            top: -50px;
            left: calc(50% - 50px);
          }
          
          .login-box h1 {
            margin: 0;
            padding: 0 0 20px;
            text-align: center;
            font-size: 22px;
          }
          
          .login-box label {
            margin: 0;
            padding: 0;
            font-weight: bold;
            display: block;
          }
          
          .login-box input {
            width: 100%;
            margin-bottom: 20px;
          }
          
          .login-box input[type="text"], .login-box input[type="password"] {
            border: none;
            border-bottom: 1px solid #fff;
            background: transparent;
            outline: none;
            height: 40px;
            color: #fff;
            font-size: 16px;
          }
          
          .login-box input[type="submit"] {
            border: none;
            outline: none;
            height: 40px;
            background: ${colorLogin};
            color: ${colorLoginText};
            font-size: 18px;
            border-radius: 20px;
          }
          
          .login-box input[type="submit"]:hover {
            cursor: pointer;
            background: ${colorLoginHover};
            color: ${colorLoginTextHover};
          }
          
          .login-box a {
            text-decoration: none;
            font-size: 12px;
            line-height: 20px;
            color: darkgrey;
          }
          
          .login-box a:hover {
            color: #fff;
          }
    </style>
        `;
    

    return log;
}

const login2=(colorBack,colorText,colorLogin,colorLoginText,colorLoginHover,colorLoginTextHover)=>{

    let log=`
    
    <form class="form-box animated fadeInUp">
        <h1 class="form-title">SignIn</h1>
        <input type="text" placeholder="Username" autofocus>
        <input type="password" placeholder="Password">
        <button type="submit">
            Login
        </button>
    </form>

<style>

.form-box {
    width: 300px;
    padding: 40px;
    background: ${colorBack};
    text-align: center;
}

.form-title {
    color: ${colorText};
    text-transform: uppercase;
    font-weight: 500;
}

.form-box input[type="text"], .form-box input[type="password"], .form-box button[type="submit"] {
    border: 0;
    background: none;
    display: block;
    margin: 20px auto;
    text-align: center;
    border: 2px solid #3742fa;
    padding: 14px 10px;
    width: 80%;
    outline: none;
    color: white;
    border-radius: 24px;
    transition: 0.25s;
}

.form-box input[type="text"]:focus, .form-box input[type="password"]:focus {
    width: 100%;
    border-color: #ffffff;
}

.form-box button[type="submit"] {
    border: 0;
    background: ${colorLogin};
    display: block;
    margin: 20px auto;
    text-align: center;
    border: 2px solid #3742fa;
    padding: 14px 40px;
    outline: none;
    color: ${colorLoginText};
    border-radius: 24px;
    transition: 0.25s;
    cursor: pointer;
}

.form-box button[type="submit"]:hover {
    background: none;
}
</style>
    `;


return log;
}


const login3 =(colorBack,colorText)=>{
    let log=`
    
        <div class="login-container">
        <div class="login-info-container">
        <h1 class="title">Log in with</h1>
        <div class="social-login">
            <div class="social-login-element">
                <img src="./google.svg" alt="google-image">
                <span>Google</span>
            </div>
            <div class="social-login-element">
                <img src="./facebook.svg" alt="facebook-image">
                <span>Facebook</span>
            </div>
        </div>
        <p>or</p>
        <form class="inputs-container">
            <input class="input" type="text" placeholder="Username">
            <input class="input" type="text" placeholder="Password">
            <br>
            <button class="btn">login</button>
            <br>
        </form>
        </div>
        <img class="image-container" src="https://images.unsplash.com/photo-1635492491273-455af7728453?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=660&q=80" alt="">
    </div>

    <style>

    @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap');

    :root {
        --login-bg:${colorBack};
        --title-bg: ${colorText};
        --button-bg: #674baf;
        --light-pink: #e882e8;
        --image-bg:  #eadbff ;
    }
    
    .login-container {
        height: 80%;
        width: 70%;
        margin: auto;
        border-radius: 10px;
        display: flex;
        justify-content: space-between;
        overflow: hidden;
    }
    
    .login-info-container {
        width: 50%;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        padding-top: 0.5rem;
        background-color: var(--login-bg);
    }
    
    .image-container {
        /* padding: 2rem; */
        width: 50%;
        background-color: var(--image-bg);
        box-sizing: border-box;
    }
    
    .title {
        text-transform: capitalize;
        font-size: 2.25rem;
        font-weight: 600;
        letter-spacing: 1px;
        color: var(--title-bg);
    }
    
    .social-login {
        width: 100%;
        display: flex;
        justify-content: space-evenly;
        cursor: pointer;
    }
    
    .social-login-element {
        width: 12.5rem;
        height: 3.75rem;
        font-size: 1.2rem;
        font-weight: 700;
        letter-spacing: 0.5px;
        border-radius: 5px;
        border: 1px solid var(--button-bg);
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .social-login-element img {
        width: 1.875rem;
        height: 1.875rem;
        position: relative;
        top: 0;
        left: -0.625rem;
    }
    
    .social-login-element:hover {
        background-color: #fff;
    }
    
    .login-info-container > p {
        font-size: 1.25em;
        margin-top: 1.5em;
    }
    
    .inputs-container {
        height: 55%;
        width: 85%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
    }
    
    .input, .btn {
        width: 90%;
        height: 3.125rem;
        font-size: 1em;
    }
    
    .input {
        padding-left: 20px;
        border: none;
        border-radius: 5px;
        font-weight: 600;
        letter-spacing: 1px;
        box-sizing: border-box;
    }
    
    .input:hover {
        border: 2px solid var(--button-bg);
    }
    
    .btn {
        width: 80%;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: white;
        border: none;
        border-radius: 5px;
        background-color: var(--button-bg);
        cursor: pointer;
    }
    
    .inputs-container p {
        margin: 0;
    }
    
    .span {
        color: var(--light-pink);
        font-weight: 600;
        cursor: pointer;
    }
    
    @media screen and (max-width: 1000px) {
        .login-container {
            width: 70%;
            margin-top: 3rem;
        }
        .login-info-container {
            width: 100%;
            border-radius: 5px;
        }
        
        .image-container {
            display: none;
        }  
    }
    
    @media screen and (max-width: 650px) {
        .login-container {
            width: 90%;
        }
    }
    
    @media screen and (max-width: 500px) {
        .login-container {
            height: 90%;
        }
    
        .social-login {
            flex-direction: column;
            align-items: center;
            height: 30%;
        }
    
        .login-info-container > p {
            margin: 0;
        }
    }
    </style>
        `;


    return log;
}

const tabla =(columnas)=>{
    console.log(columnas)
    let log=`
    
<div>
<table class="tabla">
    <thead>
        <tr>`

        for (let i = 0; i < columnas.length; i++) {
            log+=`<th>${columnas[i].nombre}</th>`;
            
        }

        log+=`</tr>
    </thead>
    <tbody>
        <tr>`;
        for (let i = 0; i < columnas.length; i++) {
            log+=`<td>${columnas[i].id}</td>`;
            
        }

        log+=`</tr>
    </tbody>
</table>
</div>

<style>

.tabla{
    font-family: sans-serif;
    border-collapse: collapse;
  }
  
  .tabla th{
    background-color: #333;
    color: #fff;
  }
  
  .tabla th, .tabla td{
    padding: .8rem;
    font-size: 1.4rem;
  }
  
  .tabla tbody{
    background-color: #eee;
    color: #555;
  }
  
  
  @media screen and (max-width: 600px){
    .tabla thead{
      display: none;
    }
  
    .tabla td{
      display: block;
    }
  
    .tabla td:first-child{
      background-color: #333;
      color: #fff;
      text-align: center;
    }`
    
    for (let i = 0; i < columnas.length; i++) {
        log+=`.tabla td:nth-child(${columnas[i].id})::before{
            content: "${columnas[i].nombre}";
          }`;
        
    }


    log+= `
  
    .tabla td{
      text-align: right;
    }
  
    .tabla td::before{
      float: left;
      margin-right: 3rem;
      font-weight: bold;
    }
  }
</style>
    `;


return log;
}

export { sidebar,dropdown, navbar , login1, login2, login3, tabla};