<template>
    <div class="w-full flex flex-row justify-around justify-items-center ite">
        <div class="h-[80vh] bg-gray-400 rounded-md w-3/12 flex flex-col justify-around justify-items-center  items-center" >
            <div>
                <p>Nombre:</p>
                <input type="text" v-model="clase" />
            </div>
            <div>
                <p>Background:</p>
                <input type="color" v-model="colorBackground"/>
                <input type="range" min="0" max="1" step="0.01" v-model="backAlpha"/>
            </div>

            <div>
                <p>Color text:</p>
                <input type="color" v-model="colorText"/>
                
            </div>

            <div>
                <p>Blur:</p>
                <input type="range" min="0" max="30" v-model="blur"/>
            </div>

            <CSS :text="modalG"/>
        </div>
        
        <CanvasSideBar :modal="modalG" />
    
    </div>

    
</template>

<script>

import CanvasSideBar from '@/components/CanvasSideBar.vue'
import CSS from '@/components/CSSgen.vue'
import {navbar} from '@/assets/func_models.js'
export default {
  components: {CanvasSideBar,CSS, },
    data() {

        return {
            clase:'navModel',
            colorText:'#ffffff',
            blur:6,
            backAlpha:0.34,
            colorBackground: '#3e65da',
            modalG:'',
        }
    },

    methods: {
        css(){
            
           this.modalG=navbar(this.hexToRgb(this.colorBackground.split('#')[1],this.backAlpha),this.blur,this.colorText,this.clase);
        },
        hexToRgb(hex,alpha) {
            var bigint = parseInt(hex, 16);
            var r = (bigint >> 16) & 255;
            var g = (bigint >> 8) & 255;
            var b = bigint & 255;

            return "rgba(" + r + "," + g + "," + b + "," + alpha+")";
        }

    },
    mounted() {
        
        this.css();
    },
    watch:{
        colorBackground(){
            this.css();
        },
        backAlpha(){
            this.css();
        },
        blur(){
            this.css();
        },
        colorText(){
            this.css();
        },
        clase(){
            this.css();
        }
        
    }

}
</script>

