<template>
    <div class="w-full flex flex-row justify-around justify-items-center ite">
        <div class="h-[80vh] bg-gray-400 rounded-md w-3/12 flex flex-col justify-around justify-items-center  items-center" >
            <div>
                <p>Direction:</p>
                <select v-model="direction">
                    <option value="flex-direction: row;"   >   Row      </option>
                    <option value="flex-direction: row-reverse;"    >   Row Reverse      </option>
                    <option value="flex-direction: column;">   Column   </option>
                    <option value="flex-direction: column-reverse;"    >   Column Reverse       </option>
                    <option value=""        >   NULL       </option>
                </select>
            </div>

            <div>
                <p>Wrap:</p>
                <select v-model="wrap">
                    <option value="flex-wrap: nowrap;"   >   No wrap      </option>
                    <option value="flex-wrap: wrap;"    >   Wrap      </option>
                    <option value="flex-wrap: wrap-reverse;">   Wrap Reverse   </option>
                    <option value=""        >   NULL       </option>
                </select>
            </div>

            <div>
                <p>Justify:</p>
                <select v-model="justifyC">
                    <option value="justify-content: flex-start;"   >   Start      </option>
                    <option value="justify-content: flex-end;"    >   End      </option>
                    <option value="justify-content: center;">   Center   </option>
                    <option value="justify-content: space-between;"    >   Between       </option>
                    <option value="justify-content: space-around;"    >   Around      </option>
                    <option value="justify-content: space-evenly;"    >   Evenly       </option>
                    <option value=""        >   NULL       </option>
                </select>
            </div>

            <div>
                <p>Justify Items:</p>
                <select v-model="justifyI">
                    <option value="justify-items: start;"   >   Start      </option>
                    <option value="justify-items: end;"    >   End      </option>
                    <option value="justify-items: center;">   Center   </option>
                    <option value="justify-items: stretch;"    >   Stretch       </option>
                    <option value=""        >   NULL       </option>
                </select>
            </div>

            <div>
                <p>Align Items:</p>
                <select v-model="AlignI">
                    <option value="align-items: flex-start;"   >   Start      </option>
                    <option value="align-items: flex-end;"    >   End      </option>
                    <option value="align-items: center;">   Center   </option>
                    <option value="align-items: stretch;"    >   Stretch      </option>
                    <option value="align-items: baseline;"    >   Baseline       </option>
                    <option value=""        >   NULL       </option>
                </select>
            </div>

            <div>
                <p>Align Content:</p>
                <select v-model="AlignC">
                    <option value="align-content: flex-start;"   >   Start      </option>
                    <option value="align-content: flex-end;"    >   End      </option>
                    <option value="align-content: center;">   Center   </option>
                    <option value="align-content: space-between;"    >   Between       </option>
                    <option value="align-content: space-around;"    >   Around      </option>
                    <option value="align-content: stretch;"    >   Stretch       </option>
                    <option value=""        >   NULL       </option>
                </select>
            </div>


            <CSS :text="styles"/>
        </div>
        
        <CanvasFlex :styles="styles" :img="img">
            
        </CanvasFlex> 
    
    </div>

    
</template>

<script>

import CanvasFlex from '@/components/CanvasFlex.vue'
import CSS from '@/components/CSSgen.vue'
export default {
  components: {CanvasFlex,CSS },
    data() {

        return {
            direction:"",
            justifyC:"",
            justifyI:"",
            AlignI:"",
            AlignC:"",
            wrap:"",
            styles:'',
        }
    },

    methods: {
        css(){

           this.styles=`
               display:flex;
               ${this.direction}
               ${this.wrap}
               ${this.justifyC}
               ${this.justifyI}
               ${this.AlignI}
               ${this.AlignC}
            `;
        },

    },
    mounted() {
        
        this.css();
    },
    watch:{
        justifyC(){
            this.css();
        },
        justifyI(){
            this.css();
        },
        AlignI(){
            this.css();
        },
        direction(){
            this.css();
        },
        wrap(){
            this.css();
        },
        AlignC(){
            this.css();
        }
        
    }

}
</script>

<style>

</style>