<template>
    <div class="w-full flex flex-row justify-around justify-items-center ">
        <div class="h-[80vh] bg-gray-400 rounded-md w-3/12 flex flex-col justify-around justify-items-center  items-center" >
            <h1>{{b1}} {{b2}} {{b3}} {{b4}}</h1>
            <input type="range" min="0" max="100" step="1" v-model="b1"/>
            <input type="range" min="0" max="100" step="1" v-model="b2"/>
            <input type="range" min="0" max="100" step="1" v-model="b3"/>
            <input type="range" min="0" max="100" step="1" v-model="b4"/>


            <CSS :text="styles"/>
        </div>
        
        <CanvasDiv :styles="styles" :img="img" :bg="bg">
            
        </CanvasDiv> 
    
    </div>

    
</template>

<script>

import CanvasDiv from '@/components/CanvasDiv.vue'
import CSS from '@/components/CSSgen.vue'
export default {
  components: {CanvasDiv,CSS },
    data() {

        return {
            b1:0,
            b2:0,
            b3:0,
            b4:0,
            img:`background-image: url(${this.url})`,
            bg:`;background-color: #ffffff;`,
            styles:'',
            color:'#ffffff',
        }
    },

    methods: {
        css(){

           this.styles=`
                border-radius: ${this.b1}% ${100-this.b1}% ${this.b3}% ${100-this.b3}% / ${100-this.b4}% ${this.b2}% ${100-this.b2}% ${this.b4}% 
            `;
        },

    },
    mounted() {
        
        this.css();
    },
    watch:{
        b1(){
            this.css();
        },
        b2(){
            this.css();
        },
        b3(){
            this.css();
        },
        b4(){
            this.css();
        }
        
    }

}
</script>

