<template>
    <div class="w-full flex flex-row justify-around justify-items-center ite">
        <div class="h-[80vh] bg-gray-400 rounded-md w-3/12 flex flex-col justify-around justify-items-center  items-center" >

            <select v-model="tipo">
                <option value="1">Tipo 1</option>
                <option value="2">Tipo 2</option>
                <option value="3">Tipo 3</option>
            </select>
            
            <div>
                <p>Color:</p>
                <input type="color" v-model="colorBack"/>
                <input type="color" v-model="colorText"/>
            </div>
            <div>
                <p>Color button:</p>
                <input type="color" v-model="colorLogin"/>
                <input type="color" v-model="colorLoginText"/>
                <br>
                <input type="color" v-model="colorLoginHover"/>
                <input type="color" v-model="colorLoginTextHover"/>
            </div>

            <CSS :text="modalG"/>
        </div>
        
        <CanvasModal :modal="modalG" />
    
    </div>

    
</template>

<script>

import CanvasModal from '@/components/CanvasModal.vue'
import CSS from '@/components/CSSgen.vue'
import {login1,login2,login3} from '@/assets/func_models.js'
export default {
  components: {CanvasModal,CSS },
    data() {

        return {
            tipo:1,

            colorBack:'#000000',
            colorText:'#ffffff',

            colorLogin:'#3742fa',
            colorLoginText:'#ffffff',
            colorLoginHover:'#ffc107',
            colorLoginTextHover:'#000000',

            modalG:'',
        }
    },

    methods: {
        css(){

            if(this.tipo=='1'){
                this.modalG=login1(this.colorBack,this.colorText,this.colorLogin,this.colorLoginText,this.colorLoginHover,this.colorLoginTextHover);  
            }else if(this.tipo=='2'){
                this.modalG=login2(this.colorBack,this.colorText,this.colorLogin,this.colorLoginText,this.colorLoginHover,this.colorLoginTextHover); 
            }else if(this.tipo=='3'){
                this.modalG=login3(this.colorBack,this.colorText);
            }
            

        },
        hexToRgb(hex) {
            var bigint = parseInt(hex, 16);
            var r = (bigint >> 16) & 255;
            var g = (bigint >> 8) & 255;
            var b = bigint & 255;

            return r + "," + g + "," + b;
        }

    },
    mounted() {
        
        this.css();
    },
    watch:{
        tipo(){
            this.css();
        },
        colorBack(){
            this.css();
        },
        colorText(){
            this.css();
        },
        colorLogin(){
            this.css();
        },
        colorLoginText(){
            this.css();
        },
        colorLoginHover(){
            this.css();
        },
        colorLoginTextHover(){
            this.css();
        },

        
    }

}
</script>

