<template>
    <div class="w-full flex flex-row justify-around justify-items-center ">
        <div class="h-[80vh] bg-gray-400 rounded-md w-3/12 flex flex-col justify-around justify-items-center  items-center" >
            <p>Background color:</p>
            <input type="color" v-model="color"/>
            <p>Rotación:</p>
            <input type="number" min="-180" max="180" step="1" v-model="rotation"/>
            <input type="range" min="-180" max="180" step="1" v-model="rotation"/>
            <p>Transparencia:</p>
            <input type="range" min="0" max="1" step="0.01" v-model="transp"/>
            <p>Blur:</p>
            <input type="range" min="0" max="50" step="1" v-model="blur"/>

            <p>Solid Border:</p>
            <input type="checkbox" v-model="check"/>
            <input type="text" v-model="url"/>
            <CSS :text="styles"/>
        </div>
        
        <CanvasDiv :styles="styles" :img="img">
            
        </CanvasDiv> 
    
    </div>

    
</template>

<script>

import CanvasDiv from '@/components/CanvasDiv.vue'
import CSS from '@/components/CSSgen.vue'
export default {
  components: {CanvasDiv,CSS },
    data() {

        return {
            color:'#ffffff',
            transp:0.5,
            rotation:0,
            blur:5,
            check:true,
            url:null,
           
            img:`background-image: url(${this.url})`,
            styles:'',
        }
    },

    methods: {
        css(){
            if(this.check){
                var borderDiv='border: 2px solid rgba( 255, 255, 255, 0.18 );';
            }else{
                var borderDiv='';
            }
            if(this.rotation != 0){
                var rot = `transform: rotate(${this.rotation}deg);`;
            }else{
                var rot = '';
            }
           this.styles=`
                background: rgba( ${this.hexToRgb(this.color.split('#')[1])}, ${this.transp} );
                box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
                backdrop-filter: blur( ${this.blur}px );
                -webkit-backdrop-filter: blur( ${this.blur}px );
                ${borderDiv}
                ${rot}
            `;
        },
        hexToRgb(hex) {
            var bigint = parseInt(hex, 16);
            var r = (bigint >> 16) & 255;
            var g = (bigint >> 8) & 255;
            var b = bigint & 255;

            return r + "," + g + "," + b;
        }
    },
    mounted() {
        
        this.css();
    },
    watch:{
        color(){
            this.css();
        }
        ,
        transp(){
            this.css();
        }
        ,
        rotation(){
            this.css();
        }
        ,
        blur(){
            this.css();
        }
        ,
        check(){
            this.css();
        }
        
    }

}
</script>

