<template>
    <div class="flex flex-col justify-center gap-4 w-full justify-items-center ">
        <div class="w-4/5 bg-black text-white overflow-y-auto h-28 m-auto">
            <h3>CSS</h3>
            <p class="text-start p-1">{{text}}</p>
        </div>

        <button class="bg-green-400 rounded-md px-8 m-auto" @click="copy">Copy</button>
    </div>

</template>

<script>

export default {
    props: {
        text: String,
    },
    data() {
        return {
            css:this.text,
        }
    },
    methods: {
        copy() {
            navigator.clipboard.writeText(this.css);
        }
    },
    watch:{
        text(){
            this.css=this.text;
        }
    }

  
}
</script>

