<template>
    <div class="w-full flex flex-row justify-around justify-items-center ite">
        <div class="h-[80vh] bg-gray-400 rounded-md w-3/12 flex flex-col justify-around justify-items-center  items-center" >
            
            <div>
                <p>Id modal:</p>
                <input type="text" v-model="idModal" />
            </div>

            <div>
                <p>Class modal:</p>
                <input type="text" v-model="ClassModal" />
            </div>
            <div>
                <p>Color modal:</p>
                <input type="color" v-model="colorModal" />
            </div>
            <div>
                <p>Color fondo modal:</p>
                <label >{{alpha}}</label>
                <div class="w-full flex justify-around flex-row gap-1">
                    <input type="range" min="0" max="1" step="0.01" v-model="alpha" class="w-full"/>
                    <input type="color" v-model="colorOverlay" />
                </div>

            </div>
            <div>
                <p>Ancho:</p>
                <div class="w-full flex justify-around flex-row gap-1">
                    <input type="number" v-model="anchoN" />
                    <select v-model="anchoTipo">
                        <option value="px"> PX </option>
                        <option value="%"> Porcent </option>
                    </select>
                </div>
            </div>

            <div>
                <p>Alto:</p>
                <div class="w-full flex justify-around flex-row gap-1">
                    <input type="number" v-model="altoN" />
                    <select v-model="altoTipo">
                        <option value="px"> PX </option>
                        <option value="%"> Porcent </option>
                    </select>
                </div>
            </div>

            <CSS :text="modalG"/>
        </div>
        
        <CanvasModal :modal="modalG" />
    
    </div>

    
</template>

<script>

import CanvasModal from '@/components/CanvasModal.vue'
import CSS from '@/components/CSSgen.vue'
export default {
  components: {CanvasModal,CSS },
    data() {

        return {
            colorModal: '#ffffff',
            colorOverlay: '#000000',
            alpha:0.8,
            altoN:300,
            altoTipo:'px',
            anchoN:300,
            anchoTipo:'px',
            idModal:'idModal',
            ClassModal:'modal',
            modalG:'',
        }
    },

    methods: {
        css(){
            //console.log(`${this.hexToRgb(this.colorOverlay.split('#')[1])}`)
           this.modalG=`
           
            <a href="#${this.idModal}"><button class="btn-${this.ClassModal}" >Modal Open</button></a>
            <div id="${this.idModal}" class="${this.ClassModal}">
                <div class="${this.ClassModal}__content">
                    <h1>Generador de modal</h1>

                    <p>
                        Test Modal
                    </p>

                    <a href="#" class="${this.ClassModal}__close">&times;</a>
                </div>
            </div>
            <style>
            #${this.idModal} {
                z-index: 999;
                visibility: hidden;
                opacity: 0;
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                display: flex;
                align-items: center;
                justify-content: center;
                background: rgba(${this.hexToRgb(this.colorOverlay.split('#')[1])}, ${this.alpha});
                transition: all .4s;
                }

                #${this.idModal}:target {
                visibility: visible;
                opacity: 1;
                }

                .${this.ClassModal}__content {
                border-radius: 4px;
                position: relative;
                width: ${this.anchoN}${this.anchoTipo};
                height: ${this.altoN}${this.altoTipo};
                background: ${this.colorModal};
                padding: 1em 2em;
                }
                .btn-${this.ClassModal}{
                background-color: #fff;
                }
                .${this.ClassModal}__close {
                position: absolute;
                top: 10px;
                right: 10px;
                color: #585858;
                text-decoration: none;
                }
            </style>
            `;
        },
        hexToRgb(hex) {
            var bigint = parseInt(hex, 16);
            var r = (bigint >> 16) & 255;
            var g = (bigint >> 8) & 255;
            var b = bigint & 255;

            return r + "," + g + "," + b;
        }

    },
    mounted() {
        
        this.css();
    },
    watch:{
        ClassModal(){
            this.css();
        },
        idModal(){
            this.css();
        },
        altoN(){
            this.css();
        },
        altoTipo(){
            this.css();
        },
        anchoN(){
            this.css();
        },
        anchoTipo(){
            this.css();
        },
        colorModal(){
            this.css();
        },
        colorOverlay(){
            this.css();
        },
        alpha(){
            this.css();
        },
        
    }

}
</script>

<style>

</style>