<template>
    <div class="w-full flex flex-row justify-around justify-items-center ite">
        <div class="h-[80vh] bg-gray-400 rounded-md w-3/12 flex flex-col justify-around justify-items-center  items-center" >
            <div>
                <p>Color Drop :</p>
                <input type="color" v-model="colorDrop"/>
                <input type="color" v-model="colorText"/>
                <input type="color" v-model="colorBorder" />
            </div>
            <div>
                <p>Color Drop hover:</p>
                <input type="color" v-model="colorHoverDrop"/>
                <input type="color" v-model="colorTextHover"/>
            </div>

            <CSS :text="modalG"/>
        </div>
        
        <CanvasModal :modal="modalG" />
    
    </div>

    
</template>

<script>

import CanvasModal from '@/components/CanvasModal.vue'
import CSS from '@/components/CSSgen.vue'
import {dropdown} from '@/assets/func_models.js'
export default {
  components: {CanvasModal,CSS },
    data() {

        return {
            colorBorder: '#e0e0e0',
            colorDrop:'#ffffff',
            colorText:'#000000',
            colorTextHover:'#ffffff',
            colorHoverDrop:'#e0e0e0',
            modalG:'',
        }
    },

    methods: {
        css(){
            
           this.modalG=dropdown(this.colorHoverDrop,this.colorTextHover,this.colorDrop,this.colorText,this.colorBorder);
        },
        hexToRgb(hex) {
            var bigint = parseInt(hex, 16);
            var r = (bigint >> 16) & 255;
            var g = (bigint >> 8) & 255;
            var b = bigint & 255;

            return r + "," + g + "," + b;
        }

    },
    mounted() {
        
        this.css();
    },
    watch:{
        colorHoverDrop(){
            this.css();
        },
        colorTextHover(){
            this.css();
        },
        colorDrop(){
            this.css();
        },
        colorText(){
            this.css();
        },
        colorBorder(){
            this.css();
        }
        
    }

}
</script>

