<script>
export default {
  props:{
    modal:String,
  },
  data() {
    return {
      
      
      
    }
  },
  methods: {
    createModal(){
     
      document.getElementById('contenedor').innerHTML = this.modal;
     
    },
    hexToRgb(hex) {
      var bigint = parseInt(hex, 16);
      var r = (bigint >> 16) & 255;
      var g = (bigint >> 8) & 255;
      var b = bigint & 255;

      return r + "," + g + "," + b;
    }

  },
  mounted() {
      this.createModal();
    },
  watch:{
    modal(){
      this.createModal();
    }
  }

}
</script>

