<template>
    <div class="w-full flex flex-row justify-around justify-items-center ite">
        <div class="h-[80vh] bg-gray-400 rounded-md w-3/12 flex flex-col justify-around justify-items-center  items-center" >
            
            <div>
                <p>Background:</p>
                <input type="color" v-model="colorBackground"/>
                <input type="range" min="0" max="1" step="0.01" v-model="backAlpha"/>
            </div>

            <div>
                <p>Perfil:</p>
                <input type="checkbox" v-model="perfil"/>
            </div>
            
            <div>
                <p>Color hover:</p>
                <input type="color" v-model="colorHover"/>
            </div>

            <div>
                <p>Num secciones:</p>
                <input type="number" min="0" v-model="secciones"/>
            </div>

            <div>
                <p>Blur:</p>
                <input type="range" min="0" max="30" v-model="blur"/>
            </div>

            <CSS :text="modalG"/>
        </div>
        
        <CanvasSideBar :modal="modalG" />
    
    </div>

    
</template>

<script>

import CanvasSideBar from '@/components/CanvasSideBar.vue'
import CSS from '@/components/CSSgen.vue'
import {sidebar} from '@/assets/func_models.js'
export default {
  components: {CanvasSideBar,CSS, },
    data() {

        return {
            blur:6,
            backAlpha:0.34,
            colorBackground: '#ffffff',
            colorHover: '#ff0000',
            secciones:2,
            perfil:true,
            modalG:'',
        }
    },

    methods: {
        css(){
            
           this.modalG=sidebar(this.perfil,this.colorHover,
           this.hexToRgb(this.colorBackground.split('#')[1],this.backAlpha),this.secciones,this.blur
           );
        },
        hexToRgb(hex,alpha) {
            var bigint = parseInt(hex, 16);
            var r = (bigint >> 16) & 255;
            var g = (bigint >> 8) & 255;
            var b = bigint & 255;

            return "rgba(" + r + "," + g + "," + b + "," + alpha+")";
        }

    },
    mounted() {
        
        this.css();
    },
    watch:{
        perfil(){
            this.css();
        },
        colorHover(){
            this.css();
        },
        colorBackground(){
            this.css();
        },
        backAlpha(){
            this.css();
        },
        blur(){
            this.css();
        },
        secciones(){
            this.css();
        }

        
    }

}
</script>

