<template>
    <div class="w-full flex flex-row justify-around justify-items-center ">
        <div class="h-[80vh] bg-gray-400 rounded-md w-3/12 flex flex-col justify-around justify-items-center  items-center" >



            <div class="flex flex-col gap-5 justify-center justify-items-center h-auto">
                <input type="text" v-model="columna" />
                <button class="bg-blue-400 rounded-md p-3" @click="addcolumna">+</button>
            </div>

            <div class="bg-gray-200 overflow-y-scroll w-full h-20" >
                <div v-for="{id,nombre} in columnas" :key="id">
                    <p>{{nombre}}<button class="bg-red-400 rounded-md px-3" @click="delcolumna(id)">-</button></p>
                </div>
            </div>
            

            <CSS :text="styles"/>

        </div>
        
        <CanvasModal :modal="styles" />
            
    
    </div>

    
</template>

<script>

import CanvasModal from '@/components/CanvasModal.vue'
import CSS from '@/components/CSSgen.vue'
import {tabla} from '@/assets/func_models.js'
export default {
  components: {CanvasModal,CSS },
    data() {

        return {
            idColumna:1,

            columnas:[],
            columna:'Titulo',
            
            
            styles:'',
            
        }
    },

    methods: {
        css(){

            if(this.columnas.length >0){
                let columnasL = ``;
                for (let i = 0; i < this.columnas.length; i++) {
                    columnasL += `,${this.columnas[i]}`;
                    
                }
                this.styles=tabla(this.columnas);
                
            }

        },
        resetValue(){

            this.columna='';
        },
        addcolumna(){
            
            this.columnas.push({id:this.idColumna,nombre:`${this.columna}`});
            //${this.porcent}%
            this.idColumna++;
            this.resetValue();
            this.css();
            
        },
        delcolumna(id){
            
            var lista = [];

            for (let i = 0; i < this.columnas.length; i++) {
                if(this.columnas[i].id != id){
                    lista.push(this.columnas[i])
                };  
            }
            this.columnas=lista;
            this.css();
        },


    },
    mounted() {
        
        this.css();
    },
    watch:{
        columnas(){
            this.css();
        }
        
    }

}
</script>

